import './App.scss';
import { useState } from 'react';

import RegistersList from './Components/RegistersList/RegistersList';
import RadioList from './Components/RadioList/RadioList';
import RadioListAd from './Components/RadioList/RadioListAd';
import RadioListStack from './Components/RadioList/RadioListStack';
import Button from './Components/Button/Button';
import Modal from './Components/Modal/Modal';
import ArrowRadio from './Components/ArrowRadio/ArrowRadio';
import Screen from './Components/Screen/Screen';

const ARRAY_LENGTH = 65536;

function App() {

  const [dataRegister, setDataRegister] = useState({ AX: "0000", BX: "0000", CX: "0000", DX: "0000" });
  const [addressRegister, setAddressRegister] = useState({ SI: "0000", DI: "0000", BP: "0000", SP: "0000", DISP: "0000" });
  const [isModalOpen, changeModalState] = useState({ status: false, index: '', value: '', submit: '' });
  const [dataIndexes, changeDataIndexes] = useState(["AX", "AX"]);
  const [memory, changeMemory] = useState(new Array(ARRAY_LENGTH));
  const [addressesHandling, changeAddresesHandling] = useState({ direction: 0, addressIndexes: ["SI", "AX"] }); //direction 0 dla rm, 1 dla mr
  const [showWarning, toggleShowWarning] = useState(false);
  const [stack, changeStack] = useState(new Array(ARRAY_LENGTH));
  const [stackHandling, handleStack] = useState({ index: 'AX' });

  const getRandomHex = () => {
    return Math.floor(Math.random() * (65536)).toString(16);
  }

  const troubleshootValue = (value) => {
    let result = value;
    const regex = /[0-9A-Fa-f]{4}/g;

    result = '0'.repeat(4 - result.length) + result;
    if (!regex.test(result)) return -1;

    return result;
  }

  const openModal = (index, type) => {
    let copy = Object.assign({}, isModalOpen);
    copy.status = !copy.status;
    copy.index = index;
    copy.value = (type === 'data') ? dataRegister[index] : addressRegister[index];
    copy.submit = (type === 'data') ? editRegisterValue : editAddressValue;
    changeModalState(copy);
  }

  const closeModal = () => {
    let copy = Object.assign({}, isModalOpen);
    copy.status = !copy.status;
    changeModalState(copy);
  }

  const editRegisterValue = (index, value) => {
    let copy = Object.assign({}, dataRegister);
    copy[index] = value;
    setDataRegister(copy);
  }

  const editAddressValue = (index, value) => {
    let copy = Object.assign({}, addressRegister);
    copy[index] = value;
    setAddressRegister(copy);
  }

  const resetDataRegisters = () => {
    let copy = Object.assign({}, dataRegister);
    for (const [key] of Object.entries(copy)) {
      copy[key] = '0000';
    }

    setDataRegister(copy);
  }

  const resetAddressRegisters = () => {
    let copy = Object.assign({}, addressRegister);
    for (const [key] of Object.entries(copy)) {
      if (key !== 'SP') copy[key] = '0000';
    }

    setAddressRegister(copy);
  }

  const randomDataRegisters = () => {
    let copy = Object.assign({}, dataRegister);
    for (const [key] of Object.entries(copy)) {
      let randomValue = getRandomHex();
      copy[key] = '0'.repeat(4 - randomValue.length) + randomValue;
    }
    setDataRegister(copy);
  }

  const handleRadioChange = e => {
    let index = e.target.name.includes('1') ? 0 : 1;
    let copy = [...dataIndexes];
    copy[index] = e.target.value;
    changeDataIndexes(copy);
  }

  const moveData = () => {
    editRegisterValue(dataIndexes[1], dataRegister[dataIndexes[0]]);
  }

  const exchangeData = () => {
    let copy = Object.assign({}, dataRegister);
    let tempValue = copy[dataIndexes[1]];
    let value = copy[dataIndexes[0]];
    copy[dataIndexes[0]] = tempValue;
    copy[dataIndexes[1]] = value;
    setDataRegister(copy);
  }

  const handleDirectionChange = e => {
    let index = parseInt(e.target.value);
    let copy = Object.assign({}, addressesHandling);
    copy.direction = index;
    changeAddresesHandling(copy);
  }

  const handleRadioChangeAD = e => {
    let index = e.target.name.includes('1') ? 0 : 1; //0-lewa , 1-prawa
    let copy = Object.assign({}, addressesHandling);
    copy.addressIndexes[index] = e.target.value;
    changeAddresesHandling(copy);
  }

  const calculateMemoryIndex = () => {
    let memoryIndex = addressesHandling.addressIndexes[0];
    let memoryIndexValue = 0;
    switch (memoryIndex) {
      case "SI":
      case "DI":
      case "BP":
        memoryIndexValue = parseInt(addressRegister[memoryIndex], 16);
        break;
      case "BX":
        memoryIndexValue = parseInt(dataRegister["BX"], 16);
        break;
      default:
        let halves = memoryIndex.split(" + ");
        console.log(memoryIndex);
        memoryIndexValue = parseInt(addressRegister[halves[0]], 16) + parseInt(((halves[1][1] === "X") ? dataRegister[halves[1]] : addressRegister[halves[1]]), 16);
        break;
    }
    memoryIndexValue += parseInt(addressRegister["DISP"], 16);
    if (memoryIndexValue > ARRAY_LENGTH - 2) {
      if (memoryIndexValue % (ARRAY_LENGTH - 1) === ARRAY_LENGTH - 1) {
        memoryIndexValue = 0;
      } else {
        memoryIndexValue = memoryIndexValue % (ARRAY_LENGTH - 1);
      }
    }

    return memoryIndexValue;
  }

  const moveMemory = () => {
    let memoryIndexValue = calculateMemoryIndex();
    if (addressesHandling.direction) { //memory -> register
      let values = [memory[memoryIndexValue], memory[memoryIndexValue + 1]];
      if (values[0] == null || values[1] == null) {
        toggleShowWarning(true);
        return;
      };
      let value = memory[memoryIndexValue] + memory[memoryIndexValue + 1];
      editRegisterValue(addressesHandling.addressIndexes[1], value);
    } else {
      let value = dataRegister[addressesHandling.addressIndexes[1]];
      let copy = [...memory];
      copy[memoryIndexValue] = value.slice(0, 2);
      copy[memoryIndexValue + 1] = value.slice(2);
      changeMemory(copy);
    }
    toggleShowWarning(false);
  }

  const exchangeMemory = () => {
    let memoryIndexValue = calculateMemoryIndex();
    let registerValue = dataRegister[addressesHandling.addressIndexes[1]];
    let values = [memory[memoryIndexValue], memory[memoryIndexValue + 1]];
    if (values[0] == null || values[1] == null) {
      toggleShowWarning(true);
      return;
    };
    let memoryValue = memory[memoryIndexValue] + memory[memoryIndexValue + 1];
    toggleShowWarning(false);
    editRegisterValue(addressesHandling.addressIndexes[1], memoryValue);
    let copy = [...memory];
    copy[memoryIndexValue] = registerValue.slice(0, 2);
    copy[memoryIndexValue + 1] = registerValue.slice(2);
    changeMemory(copy);
  }

  const handleStackChange = e => {
    let copy = Object.assign({}, stackHandling);
    copy.index = e.target.value;
    handleStack(copy);
  }

  const addSP = (value) => {
    let sp = parseInt(addressRegister["SP"], 16) + value;
    return troubleshootValue(sp.toString(16));
  }

  const push = () => {
    let indexName = stackHandling.index;
    let index = parseInt(addressRegister["SP"], 16);
    if (index > ARRAY_LENGTH - 2) return;
    let value = dataRegister[indexName];

    let copy = [...stack];
    copy[index] = value.slice(0, 2);
    copy[index + 1] = value.slice(2);
    changeStack(copy);

    let sp = addSP(2);
    editAddressValue("SP", sp);
    console.log(copy);
  }

  const pop = () => {
    let index = parseInt(addressRegister["SP"], 16);
    if (index < 1) return;

    let copy = [...stack];
    copy[index - 2] = null;
    copy[index - 1] = null;
    changeStack(copy);

    let sp = addSP(-2);
    editAddressValue("SP", sp);
  }

  return (
    <>
      <div div className="Container" >
        <h1 className="header">
          intel 8086 simulator
        </h1>
        <div className="Box Box--data">
          <div className="center">
            <RegistersList title="data registers" registers={dataRegister} handleEdit={openModal} direction="ltr" />

            <div className="buttons">
              <Button value="reset" color="black" handleClick={resetDataRegisters} />
              <Button value="random" color="pink" handleClick={randomDataRegisters} />
            </div>
          </div>
          <div className="center">
            <Button value="MOV" color="black" handleClick={moveData} />
            <RadioList handleRadioChange={handleRadioChange} />
            <Button value="XCHG" color="pink" handleClick={exchangeData} />
          </div>
        </div>
        <div className="Box Box--memory">
          <div className="center">
            <div>
              <ArrowRadio type={0} handleChange={handleDirectionChange} />
              <ArrowRadio type={1} handleChange={handleDirectionChange} />
            </div>
            <div>
              <RadioListAd handleRadioChange={handleRadioChangeAD} />
              <div className="buttons">
                <Button value="MOV" color="black" handleClick={moveMemory} />
                <Button value="XCHG" color="pink" handleClick={exchangeMemory} />
              </div>
            </div>
          </div>
          <div className="center">
            <h2 className="title">8086</h2>
            <RegistersList title="address registers" registers={addressRegister} handleEdit={openModal} direction="rtl" />
            <Button value="reset" color="black" handleClick={resetAddressRegisters} />
            <Screen title="Memory preview" value={[memory[calculateMemoryIndex()], memory[calculateMemoryIndex() + 1]]} />
          </div>
        </div>
        <div className="Box Box--stack">
          <h1>STACK</h1>
          <div className="center">
            <RadioListStack handleRadioChange={handleStackChange} />
            <div className="buttons">
              <Button value="PUSH" color="black" handleClick={push} />
              <Button value="POP" color="pink" handleClick={pop} />
            </div>
          </div>
          <div className="center">
            <Screen title="Stack Peek" value={[stack[parseInt(addressRegister["SP"], 16) - 2], stack[parseInt(addressRegister["SP"], 16) - 1]]} />
          </div>
        </div>
      </div>
      {showWarning && <div>
        The Memory data you tried to get is empty.
      </div>}
      {isModalOpen.status && <Modal troubleshootValue={troubleshootValue} index={isModalOpen.index} value={isModalOpen.value} handleSubmit={isModalOpen.submit} closeModal={closeModal} />}
    </>
  );
}

export default App;
