import React from 'react';
import styles from './RadioList.module.scss';
import RadioItem from '../RadioItem/RadioItem';

const RadioList = ({ handleRadioChange, dataSet }) => {
    return (
        <div className={styles.RadioList} onChange={handleRadioChange}>
            <div className={styles.column}>
                <RadioItem id="AX1" value="AX" name="dataIndex1" />
                <RadioItem id="BX1" value="BX" name="dataIndex1" />
                <RadioItem id="CX1" value="CX" name="dataIndex1" />
                <RadioItem id="DX1" value="DX" name="dataIndex1" />
            </div>
            <div className={styles.column}>
                <RadioItem id="AX2" value="AX" name="dataIndex2" />
                <RadioItem id="BX2" value="BX" name="dataIndex2" />
                <RadioItem id="CX2" value="CX" name="dataIndex2" />
                <RadioItem id="DX2" value="DX" name="dataIndex2" />
            </div>
        </div>
    )
}

export default RadioList;
