import React from 'react'
import styles from './RegistersList.module.scss';
import RegisterLabel from '../RegisterLabel/RegisterLabel';

const RegistersList = ({ title, registers, handleEdit, direction }) => {
    const registersItems = [];
    for (const item in registers) {
        registersItems.push(<RegisterLabel key={item} label={item} value={registers[item]} handleEdit={handleEdit} direction={direction} />);
    }

    return (
        <div className={styles.RegistersList}>
            <h2 className={styles.header}>{title}</h2>
            {registersItems}
        </div>
    )
}

export default RegistersList;
