import React from 'react'
import { useState } from 'react';
import styles from './RegisterLabel.module.scss';
import editButtonImage from '../../assets/edit.png';

const RegisterLabel = ({ label, value, handleEdit, direction }) => {
    let variant = (direction === "ltr") ? "data" : "address";
    return (
        <div className={`${styles.RegisterLabel} ${styles[direction]}`}>
            <label className={styles.label}>{label}</label>
            <div id={label} className={styles.input}>{value}</div>
            {(label !== "SP") && <button onClick={() => { handleEdit(label, variant) }} className={styles.edit}><img src={editButtonImage} alt="edit" /></button>}
        </div>
    );
}

export default RegisterLabel
