import React, { useState } from 'react'
import styles from './Modal.module.scss';
import Button from '../Button/Button';

const Modal = ({ index, value, handleSubmit, closeModal, troubleshootValue }) => {

    const [inputValue, changeValue] = useState(value);
    const [showWarning, toggleWarning] = useState(false);

    const handleInput = (value) => {
        changeValue(value);
    }

    const troubleshootSubmit = (e) => {
        e.preventDefault();
        let result = troubleshootValue(inputValue);
        if (result === -1) {
            toggleWarning(true);
        } else {
            closeModal();
            handleSubmit(index, result);
        }
    }

    return (
        <>
            <div className={styles.backDrop}></div>
            <div className={styles.Modal}>
                <div className={styles.close} onClick={closeModal}>X</div>
                <form className={styles.form} onSubmit={(e) => { troubleshootSubmit(e) }}>
                    <h2>Edit {index}</h2>
                    {showWarning ? "Wartość musi być liczbą w systemie szesnastkowym" : ""}
                    <input className={styles.input} maxLength="4" type="text" value={inputValue} onChange={e => { handleInput(e.target.value) }} />
                    <Button value="save" color="pink" />
                </form>
            </div>
        </>
    )
}

export default Modal
