import React from 'react';
import styles from './RadioList.module.scss';
import RadioItem from '../RadioItem/RadioItem';

const RadioList = ({ handleRadioChange }) => {
    return (
        <div className={styles.RadioList} onChange={handleRadioChange}>
            <div className={styles.column}>
                <RadioItem id="aSI" value="SI" name="addressIndex1" />
                <RadioItem id="aDI" value="DI" name="addressIndex1" />
                <RadioItem id="aBX1" value="BX" name="addressIndex1" />
                <RadioItem id="aBP" value="BP" name="addressIndex1" />
                <RadioItem id="aSIBX" value="SI + BX" name="addressIndex1" />
                <RadioItem id="aDIBX" value="DI + BX" name="addressIndex1" />
                <RadioItem id="aSIBP" value="SI + BP" name="addressIndex1" />
                <RadioItem id="aDIBP" value="DI + BP" name="addressIndex1" />
            </div>
            <div className={styles.column}>
                <RadioItem id="aAX" value="AX" name="addressIndex2" />
                <RadioItem id="aBX" value="BX" name="addressIndex2" />
                <RadioItem id="aCX" value="CX" name="addressIndex2" />
                <RadioItem id="aDX" value="DX" name="addressIndex2" />
            </div>
        </div>
    )
}

export default RadioList;
