import React from 'react'
import styles from './ArrowRadio.module.scss';

const ArrowRadio = ({ type, handleChange }) => {
    let id = (type) ? "mr" : "rm";
    let text = (type) ? "memory -> register" : "register -> memory";
    return (
        <div className={styles.container}>
            <input className={styles.input} value={type} name="operationDirectionRadio" type="radio" id={id} onChange={handleChange}></input>
            <label className={styles.label} htmlFor={id}>{text}</label>
        </div>
    )
}

export default ArrowRadio
