import React from 'react';
import styles from './RadioList.module.scss';
import RadioItem from '../RadioItem/RadioItem';

const RadioListStack = ({ handleRadioChange }) => {
    return (
        <div className={styles.RadioList} onChange={handleRadioChange}>
            <div className={styles.column}>
                <RadioItem id="AXs" value="AX" name="stackIndex" />
                <RadioItem id="BXs" value="BX" name="stackIndex" />
                <RadioItem id="CXs" value="CX" name="stackIndex" />
                <RadioItem id="DXs" value="DX" name="stackIndex" />
            </div>
        </div>
    )
}

export default RadioListStack;
